import IndexLayout from '../layouts';
import Wrapper from '../components/Wrapper';
import SiteNav from '../components/header/SiteNav';
import { SiteHeader, outer, inner, SiteMain } from '../styles/shared';
import * as React from 'react';
import { css } from '@emotion/core';

import { PostFullHeader, PostFullTitle, NoImage, PostFull } from '../templates/post';
import { PostFullContent } from '../components/PostContent';
import Footer from '../components/Footer';
import Helmet from 'react-helmet';


const PageTemplate = css`
  .site-main {
    background: #fff;
    padding-bottom: 4vw;
  }
  .post-full-content {
    display: grid;
    grid-gap: 1em;
    grid-template-areas: 
     "user1"
     "user2" 
  }
  .user1 {
    grid-area: user1;
  }
  .user2 {
    grid-area: user2;
  }
  .username {
    margin: 0 0 12px 0;
    padding: 0;
  }
  @media only screen and (min-width: 980px)   {
		.post-full-content {
      grid-gap: 1em;
			grid-template-columns: 45% 45%;
      grid-template-areas: "user1 user2" 
    }
  }
`;


const User = ({ children }) => (
  <div css={PageTemplate}>
    Some hotpink text.
      {children}
  </div>
)



const About: React.FunctionComponent = () => (
  <IndexLayout>
    <Helmet>
      <title>Ekibimiz</title>
    </Helmet>
    <Wrapper css={PageTemplate}>
      <header css={[outer, SiteHeader]}>
        <div css={inner}>
          <SiteNav />
        </div>
      </header>
      <main id="site-main" className="site-main" css={[SiteMain, outer]}>
        <article className="post page" css={[PostFull, NoImage]}>
          <PostFullHeader>
            <PostFullTitle>Ekibimiz ve Uzmanlık Alanları</PostFullTitle>
          </PostFullHeader>

          <PostFullContent className="post-full-content">
            <div className="user">
              <h5 className="username">Gökçin Çaylar</h5>
              <b><em>Klinik Psikolog; Prep, Attentioner Uygulayıcısı, Türkiye Disleksi Hareketi Bursa Gönüllüsü.</em></b>
              <p>
                Bursa Erkek Lisesi mezunudur. Psikoloji lisans eğitimini Uludağ Üniversitesi’nde 2006 yılında bölüm birincisi ve fakülte ikincisi olarak tamamlanmıştır. 2010 yılında Klinik Psikoloji Yüksek Lisansını Uludağ Üniversitesi Tıp Fakültesi- Psikiyatri Anabilim Dalı’nda
        <blockquote>“Üniversite Öğrencilerinin Gelecek beklentilerine Etki Eden Faktörler, Bu Faktörlerin Birbirleriyle İlişkileri ve Bireylerin Depresyon ve Kaygı Düzeylerine Etkisi”</blockquote>  konulu tezi ile tamamlamıştır. (Tez Danışmanı: Prof. Dr. Selçuk KIRLI).</p>
              <h6>İş Deneyimi</h6>
              <p>
                2006-2007  Özel Emine Örnek Koleji- okul psikoloğu, 6-7-8. Sınıflarla sınav kaygısı, ders başarısı, motivasyon, akran etkileşimi, ebeveyn ve çocuk iletişimi konularla çalışmıştır. Aile, öğretmen ve öğrencilere seminerler vermiştir.

                <ul>
                  <li>2007-2010  Uludağ Üniversitesi Hastanesi Ruh Sağlığı ve Hastalıkları A.B.D’ nda araştırma görevlisi olarak çalışmıştır.</li>
                  <li>2007-2009 döneminde Türk Psikologlar Derneği Bursa Şubesi Saymanlığını yapmıştır.</li>
                  <li>2010- 2015 Uludağ Üniversitesi Hastanesi Ruh Sağlığı Ve Hastalıkları Klinik ve Polikliniği’nde hastaların psikometrik değerlendirmelerini yapmış,  adli vakaları değerlendirmiş, ayrıca klinikte grup terapisi uygulamaları yapmıştır. Nöroloji A.B.D.’nda hastaların nöropsikolojik değerlendirmelerini yapmıştır. Çocuk Ruh Sağlığı ve Hastalıkları ve Çocuk Hematoloji Onkoloji A.B.D. ‘nda çocukların gelişimlerini değerlendirmiş, zeka testleri yapmış, kronik hastalık süreci, aile bilgilendirmesi, kayıp ve yas konularında çalışmıştır. </li>
                  <li>2018-…….   Özel Rozet Aile Danışma Merkezi</li>
                </ul>

              </p>
              <p>Mesleki Eğitimler:
                <ul>
                  <li>Wechsler Çocuklar için Zeka Ölçeği (WÇZÖ-IV) Eğitimi; Öğr. Gör. Dr. Psk. Banu Elmastaş Dikeç, Psk. Dr. Nagehan Demiral </li>
                  <li>Aile ve Evlilik Terapisi I. ve II. Seviye Kuramsal ve Uygulamalı Eğitimi; Prof. Dr. Hürol Fışıloğlu </li>
                  <li>Transaksiyonel Analiz Eğitimi; Doç Dr. Azmi Varan</li>
                  <li>T.A.T (Tematik Algı Testi) Eğitimi  ; Doç. Dr. Azmi Varan</li>
                  <li>Konuşma Bozuklukları Eğitimi;  Türk Psikologlar Derneği </li>
                  <li>Türkçe Prep Uygulayıcı Eğitimi ; Akademi Disleksi </li>
                  <li>Attentioner Dikkat Eğitimi; Çocuk ve Ergen Psikiyatristi Dr.Hülya Bingöl Çağlayan, Apamer</li>
                  <li>SAS Smart Beyin Eğitim Programı; Steven Michaelis </li>
                  <li>Bilişsel Davranışçı Terapi Eğitimi; Prof. Dr. Murad Atmaca, Pegder</li>
                  <li>Sosyal Beceri Eğitimi; Sobece </li>
                  <li>Oyun Terapisi Eğitimi; Uzman Psikolog Sibel Cesur Akyunak, Ankara Psikiyatri ve Psikoterapi AkademiÇocuk ve Ergen Danışmanlığında Yaratıcı Tekniklerin Kullanımı; Klinik Psikolog Selçuk Tokaç, Pegder</li>
                </ul>
              </p>
            </div>
            <div className="user">
              <h5 className="username">Zeliha Özata</h5>
              <b><em>Uzman Psikolog; İşyeri Psikoloğu; "Emdr, Prep, Attentioner Uygulayıcısı” </em></b>
              <p>
                Bursa Ulubatlı Hasan Anadolu Lisesi mezunudur. Psikoloji lisans eğitimini 2006 yılında Ankara Üniversitesi’nde tamamlamıştır. Lisans Tezi: Laboratuvardan Günlük Yaşama Bellek (Tez Danışmanı: Doç.Dr. Nurhan Er). 2009 yılında Sosyal Psikoloji Yüksek Lisansını da Ankara Üniversitesi’nde tamamlamıştır. Yüksek Lisans Tezi: İzlenim Ayarlamacılığı ve İş Doyumu Arasındaki İlişkinin Sosyo-ekonomik Düzeye Göre İncelenmesi (Tez Danışmanı: Prof. Dr. Ali Dönmez).</p>
              <p>
                <ul>
                  <li>2006-2008 yılları arasında TÜBİTAK-SOBAG tarafından desteklenen projede (Proje No: 106K021- Günlük Belleğin Farklı Yaş Gruplarında ve Klinik Örneklemlerde Değerlendirilmesi) yarı zamanlı “veri toplayıcı” olarak çalışmıştır. </li>
                  <li>2007-2009 yılları arasında Uludağ Üniversitesi Hastanesi Çocuk Psikiyatri Polikliniği ve Kliniğinde psikolog olarak çalışmıştır.</li>
                  <li>2009-2011 yılları arasında, İstanbul Ana Özel Eğitim ve Rehabilitasyon Merkezinde engelli çocuk, ergen ve aileleri ile çalışmıştır.</li>
                  <li>2011-2015 yılları arasında, Oyak- Renault Otomobil Fabrikaları A.Ş. İnsan Kaynakları İş Sağlığı ve Güvenliği Departmanında işyeri psikoloğu olarak çalışmıştır, mavi- beyaz yaka çalışanlara bireysel olarak psikolojik destek vermiş ve örgütün gereksinimlerine göre eğitimler düzenlemiştir.</li>
                  <li>2014 yılından beri yarı zamanlı olarak, Üstünberk Holding’ te (Martur OSB; Fompak Plastik; Fompak Siperlik Fabrikalarında) işyeri psikoloğu olarak hizmet vermeye başlamış ve halen devam etmektedir.</li>
                  <li>2016 yılında Golf Dondurma fabrikasında,</li>
                  <li>2017 yılında OMTEC fabrikasında işyeri psikoloğu olarak hizmet vermeye başlamıştır. </li>
                  <li>2018 yılında da Özel Rozet Aile Danışma Merkezi’nin kurucu ortağı olmuştur.</li>
                </ul>
              </p>
              <p>İşyeri psikoluğu olarak çalıştığı kurumlarda, kurumun gereksinimlerine göre düzenlediği sunum ve seminer başlıkları şöyledir: Stres ve Stres ile Başaçıkma Yöntemleri; Gevşeme Egzersizleri; Öfke Yönetimi; Aile içi İletişim ve Anne Baba Tutumları; Kuşaklar arası Farklılıklar; Doğal Yaşamsal Krizler ve Başaçıkma Yöntemleri; 8 Mart Emekçi Kadınlar Günü ve Toplumsal Cinsiyet; İş yerinde “Empati”, Ebeveynlikte Yeni Yaklaşımlar; Çocuğum Okula Başlıyor…</p>
              <p>Aldığı Mesleki Eğitimler:
                <ul>
                  <li>Travma ve EMDR I- II. Düzey Eğitim ve Süpervizyonu- Davranış Bilimleri Enstitüsü.</li>
                  <li>Bilişsel ve Davranışçı Terapi Kuram ve Uygulamalar/ Eğitmen: Doç. Dr. M. Hakan Türkçapar.</li>
                  <li>Aile ve Evlilik Terapisi Eğitimi/ Eğitmen: Prof. Dr. Hürol Fışıloğlu "Aile Danışmanlığı" Sertifikası..</li>
                  <li>İnsan Kaynakları Eğitimi/ Progress Eğitim Hizmetleri.</li>
                  <li>“Stroop Testi TBAG Formu, Sayı Dizisi Öğrenme Testi ve İşaretleme Testi" Sertifikası.</li>
                  <li>"Wechsler Çocuklar İçin Zeka Ölçeği (WISC-R)" Uygulama Hakkı</li>
                  <li>Prep Uygulayıcı Eğitimi, Akademidisleksi, Sas Smart Beyin Eğitim Programı, Steven Michaelis</li>
                  <li>Attentioner ile Dikkatimi Topluyorum Programı Eğitimi, Uzm. Dr. Hülya Bingöl Çağlayan, Apamer</li>
                  <li>Oyun Terapisi Eğitimi,Uzman Psikolog Sibel Cesur Akyunak, Ankara Psikiyatri ve Psikoterapi Akademi.</li>
                </ul>
                </p>
            </div>
          </PostFullContent>
        </article>
      </main>
      <Footer />
    </Wrapper>
  </IndexLayout>
);

export default About;
